import React, { Component } from 'react'
import { AppBar, Toolbar, Typography, IconButton, Dialog, DialogContent, DialogTitle, FormControl, InputLabel, Select, Input, DialogActions, Button } from '@material-ui/core';
export default class TopBar extends Component {
    state = {
        open: false
    }

    handleClose = () => {
        this.setState({
            open: false
        })
    }
    handleOpen = () => {
        this.setState({
            open: true
        })
    }
    render() {
        return (
            <AppBar position='fixed'>
                <Toolbar>
                    <Typography variant='title' color='inherit' style={{ flexGrow: 1 }}>
                        Photos
                    </Typography>
                    <div>
                        <IconButton color='inherit' onClick={this.handleOpen}>
                            <i className="material-icons">settings</i>
                        </IconButton>
                    </div>
                </Toolbar>
                <Dialog
                    disableBackdropClick
                    disableEscapeKeyDown
                    open={this.state.open}
                    onClose={this.handleClose}
                >
                    <DialogTitle>Fill the form</DialogTitle>
                    <DialogContent>
                        <form style={{display: 'flex', flexWrap: 'wrap'}}>
                            <FormControl style={{minWidth: 120}}>
                                <InputLabel htmlFor="age-native-simple">Columns:</InputLabel>
                                <Select
                                    native
                                    value={this.props.col}
                                    onChange={this.props.onChange('col')}
                                    input={<Input id="age-native-simple" />}
                                >
                                    <option value={3}>3</option>
                                    <option value={4}>4</option>
                                    <option value={5}>5</option>
                                </Select>
                            </FormControl>
                        </form>
                        <form style={{display: 'flex', flexWrap: 'wrap'}}>
                            <FormControl style={{minWidth: 120}}>
                                <InputLabel htmlFor="age-native-simple">PerPage: </InputLabel>
                                <Select
                                    native
                                    value={this.props.perPage}
                                    onChange={this.props.onChange('perPage')}
                                    input={<Input id="age-native-simple" />}
                                >
                                    <option value={20}>20</option>
                                    <option value={50}>50</option>
                                    <option value={100}>100</option>
                                </Select>
                            </FormControl>
                        </form>
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="primary">
                            Close
                        </Button>
                    </DialogActions>
                </Dialog>
            </AppBar>
        )
    }
}


