import React, { Component } from 'react'
import { GridList, GridListTile, GridListTileBar } from '@material-ui/core';

export default class List extends Component {
    constructor(props) {
        super(props);
        this.state = {
            current: null
        }
    }

    onMouseEnter = (i) => {
        this.setState({
            current: i
        })
    }
    onMouseLeave = () => {
        this.setState({
            current: null
        })
    }
    render() {
        const { tileData, col } = this.props;
        const { current } = this.state;
        return (
            <div style={{ display: 'flex', flexWrap: 'wrap', justifyContent: 'space-around', overflow: 'hidden', backgroundColor: 'white' }}>
                <GridList cellHeight={180} cols={col} style={{ width: 900, height: '100%' }}>
                    {
                        tileData.map((tile, index) =>
                            <GridListTile key={index} onMouseEnter={() => this.onMouseEnter(index)} onMouseLeave={() => this.onMouseLeave()}>
                                <img src={tile.url_n} alt={tile.title} />
                                {current === index && <GridListTileBar title={tile.title} subtitle={<span>by: {tile.ownername}<br />views: {tile.views}</span>} />}
                            </GridListTile>
                        )
                    }
                </GridList>
            </div>
        )
    }
}



