import React, { Component } from 'react'
import TopBar from '../components/TopBar';
import List from '../components/List';

export default class Photos extends Component {
    constructor(props) {
        super(props);
        this.state = {
            photo: [],
            page: 1,
            perPage: 20,
            maxPage: null,
            col: 3
        }
        this.onChange = this.onChange.bind(this);
        this.loadMore = this.loadMore.bind(this);
        this.reset = this.reset.bind(this);
    }
    async componentDidMount() {
        window.addEventListener('scroll', this.handleScroll);
        let req, res, data;
        const { page, perPage } = this.state;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=1&format=json&nojsoncallback=1`);
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: data.photo,
                page: data.page + 1,
                maxPage: data.pages
            })
        } catch (error) {
            console.log(error)
        }
    }

    componentWillUnmount() {
        window.removeEventListener('scroll', this.handleScroll);
    }

    async loadMore () {
        let req, res, data;
        const { page, photo, perPage, maxPage } = this.state;
        if(page > maxPage)
            return;
        
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=${page}&format=json&nojsoncallback=1`);
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: photo.concat(data.photo),
                page: data.page + 1,
                maxPage: data.pages
            }, () => console.log(this.state))
        } catch (error) {
            console.log(error)
        }
    }

    handleScroll = () => {
        const windowHeight = "innerHeight" in window ? window.innerHeight : document.documentElement.offsetHeight;
        const body = document.body;
        const html = document.documentElement;
        const docHeight = Math.max(body.scrollHeight, body.offsetHeight, html.clientHeight, html.scrollHeight, html.offsetHeight);
        const windowBottom = windowHeight + window.pageYOffset;
        if (windowBottom >= docHeight) {
            this.loadMore();
        }
    }

    onChange(name){
        return (event) => {
            event.preventDefault();
            this.setState({ [name]: Number(event.target.value) }, () => {
                if(name === 'perPage')  this.reset()
            });
        }
    }
    async reset () {
        let req, res, data;
        const { perPage } = this.state;
        try {
            req = await fetch(`https://api.flickr.com/services/rest/?method=flickr.interestingness.getList&api_key=ac9fd35025c930e3ea8d613813e10434&extras=owner_name%2Curl_n%2Cviews&per_page=${perPage}&page=1&format=json&nojsoncallback=1`);
            res = await req.json();
            data = res.photos;
            this.setState({
                photo: data.photo,
                page: data.page + 1,
                maxPage: data.pages
            })
        } catch (error) {
            console.log(error)
        }
    }
    render() {
        return (
            <div>
                <TopBar onChange={this.onChange} col={this.state.col} perPage={this.state.perPage}/>
                <div style={{ flex: 1, marginTop: 70 }}>
                    <List tileData={this.state.photo} col={this.state.col}/>
                </div>
            </div>
        )
    }
}



